import React, {useCallback, useEffect, useState} from "react";
import {createGlobalStyle} from "styled-components";
import {ReturnButton} from "./styledComponents/ReturnButton";
import {Weapon} from "./Weapon";
import {Character} from "./Character";
import {GreenForestEnemy} from "./GreenForestEnemy";
import {HpBar} from "./styledComponents/HpBar";
import {PlayerCard} from "./styledComponents/PlayerCard";
import {AttackButton} from "./styledComponents/AttackButton";
import {DarkScreen} from "./styledComponents/DarkScreen";
import {WinningDiv} from "./styledComponents/WinningDiv";
import {LosingDiv} from "./styledComponents/LosingDiv";
import {FloatingMessage} from "./styledComponents/FloatingMessage";
import {ShieldBar} from "./styledComponents/ShieldBar";
import {LevelCalculator} from "./LevelCalculator";

const GlobalStyle = createGlobalStyle`
    body{
        background-image : url("https://cdn.discordapp.com/attachments/696274487657758720/1000102366625935360/99810665_p0.jpg")!important;
        overflow-y: auto!important;
    }
    #GreenForestDashboard{
        border-top: 8px groove #f0f71b;
        width:102%;
        height: 200px;
        position:fixed;
        bottom:0;
        left:0;
        background-image:  linear-gradient(to bottom, rgba(180,221,180,0.5) 0%,rgba(131,199,131,0.5) 17%,rgba(82,177,82,0.5) 33%,rgba(0,138,0,0.5) 67%,rgba(0,87,0,0.5) 83%,rgba(0,36,0,0.5) 100%);
            
    }
    .buffInfo{
    margin-top: 10px;
    padding: 5px;
    width: 100%;
    height: 95px;
    border: 6px double white;
    background-color: rgba(0,0,0,0.3);
    backdrop-filter: blur(12px);
    background-origin: border-box;
    background-position: center;
    box-sizing: border-box;
    font-family: 'Edu SA Beginner', cursive;
    color: white;
    text-shadow: 2px 2px 0 black;
    display: flex;
    flex-direction: flex-start;
    gap: 10px;
    flex-wrap: wrap;
    }
    .buffBox{
    width: 75px;
    height: 75px;
    border: 2px solid white;
    background-color: rgba(0,0,0,0.3);
    backdrop-filter: blur(12px);
    background-size: contain;
    background-origin: padding-box;
    background-repeat: no-repeat;
    background-position: center;
    box-sizing: border-box;
    position: relative;
    }
    .buffTooltip{
    width: auto;
    text-align: center;
    white-space: pre;
    padding: 15px;
    border-radius: 20px;
    background-color: rgba(0,0,0,0.75);
    background-origin: border-box;
    box-sizing: border-box;
    color: white;
    font-family: 'Edu SA Beginner', cursive;
    position: absolute;
    z-index: 10;
    left : 0;
    bottom: 120px;
    transition: opacity 0.5s ease 0s;
    opacity: 0%;
    }
    .attackbtn{
        position:relative;
    }
    .attackbtn > kbd{
        position: absolute;
        bottom: 0;
        left: 0;
    }
`
function GreenForest(props){
    const [heroBaseHp,setHeroBaseHp] = useState(1);
    const [heroShield,setHeroShield] = useState(true);
    const [heroBaseShield,setHeroBaseShield] = useState(0);
    const [heroBaseDamage,setHeroBaseDamage] = useState(1);
    const [heroHp,setHeroHp] = useState(1);
    const [heroDamage,setHeroDamage] = useState(1);
    const [heroShieldHp,setHeroShieldHp] = useState(1);
    const [enemyBaseHp,setEnemyBaseHp] = useState(1);
    const [enemyShield,setEnemyShield] = useState(false);
    const [enemyBaseShield,setEnemyBaseShield] = useState(1);
    const [enemyBaseDamage,setEnemyBaseDamage] = useState(1);
    const [enemyHp,setEnemyHp] = useState(1);
    const [enemyDamage,setEnemyDamage] = useState(1);
    const [enemyShieldHp,setEnemyShieldHp] = useState(1);
    const [heroTurn,setHeroTurn] = useState(true);
    const [heroSideText,setHeroSideText] = useState();
    const [enemySideText,setEnemySideText] = useState();
    const [enemyTurn,setEnemyTurn] = useState(false)
    let gameinfo = {area:"GreenForest",
        heroBaseHp,
        setHeroBaseHp,
        heroBaseDamage,
        setHeroBaseDamage,
        heroHp,
        setHeroHp,
        heroDamage,
        setHeroDamage,
        enemyBaseHp,
        setEnemyBaseHp,
        enemyBaseDamage,
        setEnemyBaseDamage,
        enemyHp,
        setEnemyHp,
        enemyDamage,
        setEnemyDamage,
        setHeroSideText,
        setEnemySideText,
        heroShield,
        setHeroShield,
        heroBaseShield,
        setHeroBaseShield,
        heroShieldHp,
        setHeroShieldHp,
        enemyShield,
        setEnemyShield,
        enemyBaseShield,
        setEnemyBaseShield,
        enemyShieldHp,
        setEnemyShieldHp,
        enemyTurn,
        setEnemyTurn}
    let weapon,character,Enemy,selectedCharacter,selectedWeapon,selectedEnemy;
    const [SelectedCharacter,setSelectedCharacter] = useState(new Character(gameinfo));
    const [SelectedEnemy,setSelectedEnemy] = useState(new GreenForestEnemy(gameinfo,props.dataControl.Data));
    const [SelectedWeapon,setSelectedWeapon] = useState(new Weapon(gameinfo));
    useEffect(()=>{
        weapon = new Weapon(gameinfo);
        character = new Character(gameinfo);
        Enemy = new GreenForestEnemy(gameinfo,props.dataControl.Data);
        //console.log(props.dataControl.Data);
        selectedCharacter = character.findCharacter(props.dataControl.Data.selectedCharacter,props.dataControl.Data.characters[props.dataControl.Data.selectedCharacter]);
        selectedWeapon = weapon.findWeapon(props.dataControl.Data.selectedWeapon,props.dataControl.Data.weapons[props.dataControl.Data.selectedWeapon]);
        selectedEnemy = Enemy.makeEnemy();
        setHeroBaseHp(selectedCharacter.baseHp);
        setHeroBaseDamage(selectedWeapon.damage);
        setHeroHp(selectedCharacter.baseHp);
        setHeroDamage(selectedWeapon.damage);
        setEnemyBaseHp(selectedEnemy.baseHp);
        setEnemyBaseDamage(selectedEnemy.baseDmg);
        setEnemyHp(selectedEnemy.baseHp);
        setEnemyDamage(selectedEnemy.baseDmg);
        setHeroShield(selectedCharacter.shield)
        setEnemyShield(selectedEnemy.shield);
        if(selectedCharacter.shield){
            setHeroBaseShield(selectedCharacter.baseShield);
            setHeroShieldHp(selectedCharacter.baseShield);
        }
        if(selectedEnemy.shield){
            setEnemyBaseShield(selectedEnemy.baseShield);
            setEnemyShieldHp(selectedEnemy.baseShield);
        }
        selectedCharacter.updateginfo(gameinfo);
        setSelectedCharacter(selectedCharacter);
        setSelectedEnemy(selectedEnemy);
        setSelectedWeapon(selectedWeapon);
    },[])
    function NormalAttack(){
        SelectedCharacter.normalAttack();
        //EnemyTurn();
        SelectedCharacter.updateginfo(gameinfo);
    }
    function ChargedAttack(){
        SelectedCharacter.chargedAttack();
        //EnemyTurn();
        SelectedCharacter.updateginfo(gameinfo);
    }
    function Skill1Attack(){
        SelectedCharacter.skill1Attack();
        //EnemyTurn();
        SelectedCharacter.updateginfo(gameinfo);
    }
    function Skill2Attack(){
        SelectedCharacter.skill2Attack();
        //EnemyTurn();
        SelectedCharacter.updateginfo(gameinfo);
    }
    useEffect(()=>{
        if(enemyTurn){
            setHeroTurn(false);
            setTimeout(()=>{
                //console.log("enemy attacks")
                let hitDmg = enemyDamage + Math.floor((Math.random()-0.5)*(enemyDamage/10));
                setHeroSideText(<> </>);
                if(SelectedCharacter.shield){
                    setHeroSideText(<><FloatingMessage><span className="gray-text">-{hitDmg} Shield</span></FloatingMessage></>)
                    setHeroShieldHp(prevState => {
                        return prevState - hitDmg
                    });
                }else {
                    setHeroSideText(<><FloatingMessage><span className="red-text">-{hitDmg} Health</span></FloatingMessage></>)
                    setHeroHp(prevState => {
                        return prevState - hitDmg
                    });
                }
                SelectedEnemy.enemyPassive(gameinfo);
                SelectedCharacter.afterEnemyAttack(gameinfo);
                setEnemyTurn(false)
            },1000)
            setTimeout(()=>{
                //console.log("hero turn now")
                setHeroTurn(true);
            },1500)
        }
    },[enemyTurn])
    useEffect(()=>{
        if(heroShieldHp <=0){
            setHeroShield(false);
            SelectedCharacter.shield = false;
        }
    },[heroShieldHp])
    useEffect(()=>{
        if(enemyShieldHp <=0){
            setEnemyShield(false);
        }
    },[enemyShieldHp])
    let endingScene;
    if(enemyHp<=0){
        let data = {...props.dataControl.Data};
        let oldlvl = LevelCalculator(data.xp).level;
        data.xp = data.xp + SelectedEnemy.rewardXp;
        let newlvl = LevelCalculator(data.xp).level;
        if(newlvl > oldlvl){
            data.coin = data.coin + 100;
        }
        data.coin = data.coin + SelectedEnemy.rewardCoin;
        localStorage.setItem("SimpleImpactData",JSON.stringify(data));
        endingScene = <DarkScreen>
            <WinningDiv>
                <h3>Congratulations!<br/>You beat the enemy!</h3>
                <h4>Your rewards:</h4>
                <h4>XP +{SelectedEnemy.rewardXp}</h4>
                <div>
                    <svg style={{color:"green"}} xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="currentColor"
                         className="bi bi-coin" viewBox="0 0 16 16">
                        <path
                            d="M5.5 9.511c.076.954.83 1.697 2.182 1.785V12h.6v-.709c1.4-.098 2.218-.846 2.218-1.932 0-.987-.626-1.496-1.745-1.76l-.473-.112V5.57c.6.068.982.396 1.074.85h1.052c-.076-.919-.864-1.638-2.126-1.716V4h-.6v.719c-1.195.117-2.01.836-2.01 1.853 0 .9.606 1.472 1.613 1.707l.397.098v2.034c-.615-.093-1.022-.43-1.114-.9H5.5zm2.177-2.166c-.59-.137-.91-.416-.91-.836 0-.47.345-.822.915-.925v1.76h-.005zm.692 1.193c.717.166 1.048.435 1.048.91 0 .542-.412.914-1.135.982V8.518l.087.02z"/>
                        <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                        <path d="M8 13.5a5.5 5.5 0 1 1 0-11 5.5 5.5 0 0 1 0 11zm0 .5A6 6 0 1 0 8 2a6 6 0 0 0 0 12z"/>
                    </svg> <h4 style={{display:"inline",fontFamily:"sans-serif"}}>+{SelectedEnemy.rewardCoin}</h4>
                </div>
                {(newlvl > oldlvl) ?  <><h4>Level UP!</h4>
                    <div>
                        <svg style={{color:"green"}} xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="currentColor"
                             className="bi bi-coin" viewBox="0 0 16 16">
                            <path
                                d="M5.5 9.511c.076.954.83 1.697 2.182 1.785V12h.6v-.709c1.4-.098 2.218-.846 2.218-1.932 0-.987-.626-1.496-1.745-1.76l-.473-.112V5.57c.6.068.982.396 1.074.85h1.052c-.076-.919-.864-1.638-2.126-1.716V4h-.6v.719c-1.195.117-2.01.836-2.01 1.853 0 .9.606 1.472 1.613 1.707l.397.098v2.034c-.615-.093-1.022-.43-1.114-.9H5.5zm2.177-2.166c-.59-.137-.91-.416-.91-.836 0-.47.345-.822.915-.925v1.76h-.005zm.692 1.193c.717.166 1.048.435 1.048.91 0 .542-.412.914-1.135.982V8.518l.087.02z"/>
                            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                            <path d="M8 13.5a5.5 5.5 0 1 1 0-11 5.5 5.5 0 0 1 0 11zm0 .5A6 6 0 1 0 8 2a6 6 0 0 0 0 12z"/>
                        </svg> <h4 style={{display:"inline",fontFamily:"sans-serif"}}>+100</h4>
                    </div></> : null}
            </WinningDiv>
        </DarkScreen>
    }else if(heroHp<=0){
        endingScene = <DarkScreen><LosingDiv><h1 style={{color:"white"}}>You died to enemy.</h1></LosingDiv></DarkScreen>
    }else{
        endingScene = null;
    }
    const AttackFunc = useCallback((e) =>{//all attacks key
        if(e.code==="KeyQ"){
            NormalAttack();
            document.removeEventListener('keyup',AttackFunc);
        }else if(e.code==="KeyW"){
            ChargedAttack();
            document.removeEventListener('keyup',AttackFunc);
        }else if(e.code==="KeyE" && SelectedCharacter.skill1avail){
            Skill1Attack();
            document.removeEventListener('keyup',AttackFunc);
        }else if(e.code==="KeyR" && SelectedCharacter.skill2avail){
            Skill2Attack();
            document.removeEventListener('keyup',AttackFunc);
        }

    },[SelectedCharacter,enemyHp,heroHp,enemyShield,heroShield])
    useEffect(()=>{//deleting and reusing
            if(heroTurn){
                document.addEventListener('keyup',AttackFunc);
            }else{
                document.removeEventListener('keyup',AttackFunc);
            }
    },[heroTurn,enemyHp,heroHp,enemyShield,heroShield])
    const returnBtnFunc = useCallback((e) =>{//return to main menu
        if(e.code==="Escape"){
            props.sceneControl.setScene("MainMenu");
            document.removeEventListener('keyup',returnBtnFunc);
            document.removeEventListener('keyup',AttackFunc);
        }
    },[])
    useEffect(()=>{//applying only once
        document.addEventListener('keyup',returnBtnFunc);
    },[])
    //removing this for every scene changer
    return(<>
        <GlobalStyle/>
        <ReturnButton onClick={()=>{document.removeEventListener('keyup',AttackFunc);document.removeEventListener('keyup',returnBtnFunc);props.sceneControl.setScene("MainMenu")}}>
            <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" fill="currentColor"
                 className="bi bi-reply-fill" viewBox="0 0 16 16">
                <path
                    d="M5.921 11.9 1.353 8.62a.719.719 0 0 1 0-1.238L5.921 4.1A.716.716 0 0 1 7 4.719V6c1.5 0 6 0 7 8-2.5-4.5-7-4-7-4v1.281c0 .56-.606.898-1.079.62z"/>
            </svg>
        </ReturnButton>
        <div className={"row justify-content-center"}>
            <div className={"col-5"} style={{margin:0,maxWidth: "400px"}}>
                <PlayerCard turn={heroTurn} imgsrc={SelectedCharacter.imgsrc}/>
                <HpBar type="hero" cvalue={heroHp} maxvalue={heroBaseHp}>{(heroHp<0) ? 0 : heroHp}/{heroBaseHp}</HpBar>
                {(heroShield) ? <ShieldBar cvalue={heroShieldHp} maxvalue={heroBaseShield}>{(heroShieldHp<0) ? 0 : heroShieldHp}/{heroBaseShield}</ShieldBar> : null}
                <div className="buffInfo" id="heroBuff">

                </div>
            </div>
            <div id="HeroSide" className={"col-1 d-flex flex-column justify-content-end position-relative"}>
                {heroSideText}
            </div>
            <div id="EnemySide" className={"col-1 d-flex flex-column justify-content-end align-items-end position-relative"}>
                {enemySideText}
            </div>
            <div className={"col-5"} style={{margin:0,maxWidth: "400px"}}>
                <PlayerCard turn={!heroTurn} imgsrc={SelectedEnemy.imgsrc}>
                    <h3 style={{color:"white",margin:"10px auto",maxWidth:"350px",width:"100%",boxShadow:"0 0 10px 10px rgba(0,0,0,0.5)",backgroundColor:"rgba(0,0,0,0.5)",borderRadius:"50%"}}>{SelectedEnemy.name}</h3>
                </PlayerCard>
                <HpBar type="enemy" cvalue={enemyHp} maxvalue={enemyBaseHp}>{(enemyHp<0) ? 0 : enemyHp}/{enemyBaseHp}</HpBar>
                {(enemyShield && enemyShieldHp > 0) ? <ShieldBar cvalue={enemyShieldHp} maxvalue={enemyBaseShield}>{(enemyShieldHp<0) ? 0 : enemyShieldHp}/{enemyBaseShield}</ShieldBar> : null}
            </div>
        </div>
        <div style={{height: "210px",visibility:"hidden"}}>
            space for dashboard
        </div>
        <div id="GreenForestDashboard" className={"row justify-content-around align-items-center"}>
            <AttackButton title="Normal Attack" className={"col-md-3 col-6 attackbtn"} imgsrc={SelectedWeapon.imgsrc} readystat={heroTurn} available={true} onClick={()=>{if (heroTurn) {NormalAttack()}}}><kbd className="d-lg-inline d-none">Q</kbd></AttackButton>
            <AttackButton title="Charged Attack" className={"col-md-3 col-6 attackbtn"} type="charged" readystat={heroTurn} available={true} onClick={()=>{if (heroTurn) {ChargedAttack()}}}><kbd className="d-lg-inline d-none">W</kbd></AttackButton>
            {(SelectedCharacter.skill1) ? <AttackButton title={SelectedCharacter.skill1name} className={"col-md-3 col-6 attackbtn"} type="" imgsrc={SelectedCharacter.skill1imgsrc} readystat={heroTurn} available={SelectedCharacter.skill1avail} onClick={()=>{if (heroTurn && SelectedCharacter.skill1avail) {Skill1Attack()}}}><kbd className="d-lg-inline d-none">E</kbd></AttackButton> : null}
            {(SelectedCharacter.skill2) ? <AttackButton title={SelectedCharacter.skill2name} className={"col-md-3 col-6 attackbtn"} type="" imgsrc={SelectedCharacter.skill2imgsrc} readystat={heroTurn} available={SelectedCharacter.skill2avail} onClick={()=>{if (heroTurn && SelectedCharacter.skill2avail) {Skill2Attack()}}}><kbd className="d-lg-inline d-none">R</kbd></AttackButton> : null}
        </div>
        {endingScene}
    </>)
}
export default GreenForest