import React, {useState, useEffect, useCallback} from "react";
import styled, {createGlobalStyle} from "styled-components";
import MainMenu from "./MainMenu";
import AreaSelection from "./AreaSelection";
import GreenForest from "./GreenForest";
import CharacterSelection from "./CharacterSelection";
import WeaponSelection from "./WeaponSelection";
import Shop from "./Shop";
import CharacterArchive from "./CharacterArchive";
import WeaponArchive from "./WeaponArchive";
import DarkOcean from "./DarkOcean";
const GlobalStyles = createGlobalStyle`
        body{
        background-image: url("https://cdn.discordapp.com/attachments/696274487657758720/999679084378472614/97779776_p0.jpg");
        background-attachment: fixed!important;
        background-size: 100vw 100vh;
        background-repeat: no-repeat;
        overflow: hidden;
        }
        .red-text{
            color: red;
        }
        .green-text{
            color: green;
        }
        .gray-text{
            color: gray;
        }
        .blue-text{
            color: lightblue;
        }
        .uncommon-text{
            color: #7e7e7e;
        }
        .rare-text{
            color: #00a7ff;
        }
        .legendary-text{
            color: #bd1fff;
        }
    `
function SimpleImpact(){
    const [Data,setData] = useState({});
    const [scene,setScene] = useState("MainMenu");
    useEffect(()=>{//getting data from local storage
        if(localStorage.SimpleImpactData!==undefined){
            setData(JSON.parse(localStorage.SimpleImpactData));
        }else{
            setData({xp:0,coin:0,weapons:{},characters:{},selectedCharacter:"",selectedWeapon:"",chest:["ch001","ch002"]});
            localStorage.setItem("SimpleImpactData",JSON.stringify({xp:0,coin:0,weapons:{},characters:{},selectedCharacter:"",selectedWeapon:"",chest:["ch001","ch002"]}));}
    },[localStorage,scene])
    let Scene = <></>;
    if(scene==="MainMenu"){
        Scene = <MainMenu dataControl={{Data,setData}} sceneControl={{scene,setScene}}/>;
    }else if(scene==="AreaSelection"){
        Scene = <AreaSelection dataControl={{Data,setData}} sceneControl={{scene,setScene}}/>
    }else if(scene==="GreenForest"){
        Scene = <GreenForest dataControl={{Data,setData}} sceneControl={{scene,setScene}}/>
    }else if(scene==="DarkOcean"){
        Scene = <DarkOcean dataControl={{Data,setData}} sceneControl={{scene,setScene}}/>
    }else if(scene==="CharacterSelection"){
        Scene = <CharacterSelection dataControl={{Data,setData}} sceneControl={{scene,setScene}}/>
    }else if(scene==="WeaponSelection"){
        Scene = <WeaponSelection dataControl={{Data,setData}} sceneControl={{scene,setScene}}/>
    }else if(scene==="Shop"){
        Scene = <Shop dataControl={{Data,setData}} sceneControl={{scene,setScene}}/>
    }else if(scene==="CharacterArchive"){
        Scene = <CharacterArchive dataControl={{Data,setData}} sceneControl={{scene,setScene}}/>
    }else if(scene==="WeaponArchive"){
        Scene = <WeaponArchive dataControl={{Data,setData}} sceneControl={{scene,setScene}}/>
    }
    return(
        <>
            <GlobalStyles/>
            {Scene}

        </>
    )
}
export default SimpleImpact