import styled from "styled-components";
export const AttackButton = styled.div`
    width: 120px;
    height: 120px;
    border: 5px ridge ${(props) => {if(props.readystat && props.available){return "#f0f71b"}else{return "#737373"}}};
    background-image: url("${(props) => {if(props.type==="normal"){return "https://cdn.discordapp.com/attachments/696274487657758720/1001205676170817556/normal_attack.jpg"}else if(props.type==="charged"){return "https://cdn.discordapp.com/attachments/696274487657758720/1001205706168483850/charged_attack.jpg"}else{return props.imgsrc}}}");
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    box-sizing: border-box;
    background-color: black;
    `