import styled from "styled-components";
export const AreaSelectionButton = styled.div`
    background-image: url("${(props)=>{return props.imgsrc}}");
    background-size: 100% 130%;
    background-repeat: no-repeat;
    background-position: center;
    background-origin: border-box;
    width: 100%;
    height: 200px;
    min-width: 250px;
    max-width: 700px;
    border: 2px solid ${(props)=>{return props.borderColor}};
    border-radius: 100px;
    font-size: 80px;
    font-family: ${(props)=>{return props.font}};
    color: ${(props)=>{return props.color}};
    text-shadow: 3px 3px 5px ${(props)=>{return props.shadowColor}};
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-size 0.5s ease-out 0s;
    position: relative;
    margin: 0 20px;
    cursor: pointer;
    box-shadow: 0px 0px 10px 10px ${(props)=>{return props.borderColor}};
    &:hover{
        background-size: 125% 160%;
    }
`