import React, {useState, useEffect, useCallback} from "react";
import {createGlobalStyle} from "styled-components";
import {XpBar} from "./styledComponents/XpBar";
import {StyledButton} from "./styledComponents/StyledButton";
import {ReturnButton} from "./styledComponents/ReturnButton";
import {CharacterCard} from "./styledComponents/CharacterCard";
import {Character} from "./Character";
import {AttackButton} from "./styledComponents/AttackButton";
import {HpBar} from "./styledComponents/HpBar";
import {SelectButton} from "./styledComponents/SelectButton";
import {LevelCalculator} from "./LevelCalculator";
const GlobalStyles = createGlobalStyle`
        body{
        overflow-y: auto!important;
        }
        div#profile{
            width: 250px;
            height: 120px;
            position: absolute;
            left: 0;
            top : 0;
            background-color: rgba(255,255,255,0.7);
            border-radius: 20px;
            box-shadow: 0 0 10px 10px rgba(255,255,255,0.7);
            font-size: 26px;
            font-family: Tisa sans-serif;
            text-align: left;
            padding: 10px 30px;
        }
        span#level{
            background-image: url("https://cdn.discordapp.com/attachments/696274487657758720/999698453808558171/156599-vector-star-gold-free-transparent-image-hq.png");
            background-size: 60px 60px;
            display: inline-block;
            background-repeat: no-repeat;
            width: 60px;
            height: 60px;
            text-align: center;
            padding-top: 13px;
            font-size: 26px;
            font-family: Tisa sans-serif;
        }
        .characterLevel{
            margin: 10px;
            height:30px;
            padding: 0;
            display:flex;
            gap: 5px;
            justify-content: center;
            align-items: center;
            position:relative;
            float:left;
            color: yellow;
            background-color: rgba(0,0,0,0.4);
            border-radius: 50%;
            box-shadow: 0 0 10px 10px rgba(0,0,0,0.4);
        }
        .characterName{
            position:absolute;
            padding: 0 10px;
            bottom: 0;
            left: 0;
            text-shadow: 0px 0px 5px antiquewhite;
        }
        .infoSection{
            padding: 10px;
            background-color: rgba(0,0,0,0.3);
            backdrop-filter: blur(5px);
            border: 5px double antiquewhite;
            border-radius: 8px;
            color: antiquewhite;
            text-shadow: 0 0 5px black;
            font-size: 1.25em;
            font-family: 'Edu SA Beginner', cursive;
            min-width: 300px;
            margin-bottom: 10px;
        }
    `
function CharacterSelection(props){
    const returnBtnFunc = useCallback((e) =>{//return to main menu
        if(e.code==="Escape"){
            props.sceneControl.setScene("MainMenu");
            document.removeEventListener('keyup',returnBtnFunc);
        }
    },[])
    useEffect(()=>{//applying only once
        document.addEventListener('keyup',returnBtnFunc);
    },[])
    //removing this for every scene changer
    let characters = {...props.dataControl.Data.characters};
    const [selectedPreview,setSelectedPreview] = useState(new Character());
    useEffect(()=>{
        if(props.dataControl.Data.selectedCharacter.toString().charAt(0)==="c"){
            setSelectedPreview(Character.prototype.findCharacter(props.dataControl.Data.selectedCharacter, characters[props.dataControl.Data.selectedCharacter]))
        }
    },[])
        function changeCharacter(){
        let oldData = {...props.dataControl.Data};
        oldData.selectedCharacter = selectedPreview.code;
        props.dataControl.setData(oldData);
        localStorage.setItem("SimpleImpactData",JSON.stringify(oldData));
    }
    function getStar(level){
        let result = <></>;
        for(let i=0;i<level;i++){
            result = <> {result}<svg xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="currentColor" className="bi bi-star" viewBox="0 0 16 16">
                <path d="M2.866 14.85c-.078.444.36.791.746.593l4.39-2.256 4.389 2.256c.386.198.824-.149.746-.592l-.83-4.73 3.522-3.356c.33-.314.16-.888-.282-.95l-4.898-.696L8.465.792a.513.513 0 0 0-.927 0L5.354 5.12l-4.898.696c-.441.062-.612.636-.283.95l3.523 3.356-.83 4.73zm4.905-2.767-3.686 1.894.694-3.957a.565.565 0 0 0-.163-.505L1.71 6.745l4.052-.576a.525.525 0 0 0 .393-.288L8 2.223l1.847 3.658a.525.525 0 0 0 .393.288l4.052.575-2.906 2.77a.565.565 0 0 0-.163.506l.694 3.957-3.686-1.894a.503.503 0 0 0-.461 0z"/>
            </svg></>
        }
        return result
    }
    let levelInfo = LevelCalculator(props.dataControl.Data.xp);
    return(<>
        <GlobalStyles/>
        <div id="profile">
            <div style={{display:"flex",justifyContent:"flex-start",alignItems:"center"}}>
                    <span id="level">
                        {levelInfo.level}
                    </span>
                <XpBar maxvalue={levelInfo.limit} cvalue={levelInfo.remaining}>{levelInfo.remaining}/{levelInfo.limit}</XpBar>
            </div>
            <div>
                <svg style={{color:"green"}} xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="currentColor"
                     className="bi bi-coin" viewBox="0 0 16 16">
                    <path
                        d="M5.5 9.511c.076.954.83 1.697 2.182 1.785V12h.6v-.709c1.4-.098 2.218-.846 2.218-1.932 0-.987-.626-1.496-1.745-1.76l-.473-.112V5.57c.6.068.982.396 1.074.85h1.052c-.076-.919-.864-1.638-2.126-1.716V4h-.6v.719c-1.195.117-2.01.836-2.01 1.853 0 .9.606 1.472 1.613 1.707l.397.098v2.034c-.615-.093-1.022-.43-1.114-.9H5.5zm2.177-2.166c-.59-.137-.91-.416-.91-.836 0-.47.345-.822.915-.925v1.76h-.005zm.692 1.193c.717.166 1.048.435 1.048.91 0 .542-.412.914-1.135.982V8.518l.087.02z"/>
                    <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                    <path d="M8 13.5a5.5 5.5 0 1 1 0-11 5.5 5.5 0 0 1 0 11zm0 .5A6 6 0 1 0 8 2a6 6 0 0 0 0 12z"/>
                </svg> {props.dataControl.Data.coin}
            </div>
        </div>
        <ReturnButton onClick={()=>{document.removeEventListener('keyup',returnBtnFunc);props.sceneControl.setScene("MainMenu")}}>
            <svg xmlns="http://www.w3.org/2000/svg" width="50" height="50" fill="currentColor"
                 className="bi bi-reply-fill" viewBox="0 0 16 16">
                <path
                    d="M5.921 11.9 1.353 8.62a.719.719 0 0 1 0-1.238L5.921 4.1A.716.716 0 0 1 7 4.719V6c1.5 0 6 0 7 8-2.5-4.5-7-4-7-4v1.281c0 .56-.606.898-1.079.62z"/>
            </svg>
        </ReturnButton>
        <div className="row d-flex justify-content-center" style={{marginTop:"125px",padding:"0 20px"}}>
            <div className="col-lg-3 col-12 d-flex flex-column align-items-center">
                <CharacterCard imgsrc={selectedPreview.imgsrc} star={selectedPreview.star}>
                    <div className="characterLevel">
                        {getStar(characters[selectedPreview.code])}
                    </div>
                </CharacterCard>
                <HpBar style={{maxWidth:"350px",minWidth:"300px"}} type="hero" cvalue={100} maxvalue={100}>Health: {selectedPreview.baseHp}</HpBar>
                <h3 style={{margin:"10px 0",maxWidth:"350px",minWidth:"300px",boxShadow:"0 0 10px 10px antiquewhite",backgroundColor:"antiquewhite",borderRadius:"50%"}}>{selectedPreview.name}</h3>
                <div className="infoSection" style={{minWidth:"300px",margin:"10px 25px"}}>
                    {selectedPreview.info}
                </div>
                {(selectedPreview.skill1) ? <div className="infoSection">
                    <AttackButton imgsrc={selectedPreview.skill1imgsrc} style={{float:"left"}}/>
                    <h3>{selectedPreview.skill1name}</h3>
                    {selectedPreview.skill1info(characters[selectedPreview.code])}
                </div> : null}
                {(selectedPreview.skill2) ? <div className="infoSection">
                    <AttackButton imgsrc={selectedPreview.skill2imgsrc} style={{float:"left"}}/>
                    <h3>{selectedPreview.skill2name}</h3>
                    {selectedPreview.skill2info(characters[selectedPreview.code])}
                </div> : null}
                {(selectedPreview.passive) ? <div className="infoSection">
                    <h3>Passive Skill</h3>
                    {selectedPreview.passiveinfo(characters[selectedPreview.code])}
                </div> : null}
                <div style={{margin:"10px 0"}}>
                    <SelectButton selected={(selectedPreview.code===props.dataControl.Data.selectedCharacter)} onClick={changeCharacter}>{(selectedPreview.code===props.dataControl.Data.selectedCharacter) ? "Selected" : "Select"}</SelectButton>
                </div>
            </div>
            <div className="col-lg-8 col-12 d-flex justify-content-start align-items-start" style={{height:"fit-content",margin:"0 20px",gap:"0 20px",flexWrap:"wrap",border:"10px ridge gold",padding:"10px",backgroundColor:"rgba(0,0,0,0.2)",backdropFilter:"blur(5px)"}}>
                {Object.keys(characters).map((character)=>{return <CharacterCard key={character} onClick={()=>{setSelectedPreview(Character.prototype.findCharacter(character,characters[character]))}} style={{cursor:"pointer"}} imgsrc={Character.prototype.findCharacter(character,1).imgsrc} star={Character.prototype.findCharacter(character,1).star}>
                    <div className="characterLevel">
                        {getStar(characters[character])}
                    </div>
                    <h3 className="characterName">{Character.prototype.findCharacter(character,1).name}</h3>
                </CharacterCard>})}
            </div>
        </div>
    </>)
}
export default CharacterSelection;