import styled from "styled-components";
export const ReturnButton = styled.button`
    position: fixed;
    top: 0;
    right: 0;
    margin: 10px;
    color: #3883c9;
    background-color: rgba(190,208,234,1);
    width: 100px;
    height: 100px;
    border: 7px solid rgba(121,155,200,1);
    border-radius: 50%;
    z-index: 1000;
    &:hover{
    border: 7px solid #3883c9;
    }
    &:active{
        border: 7px solid #7e8b9e;
    }
`