import styled from "styled-components";
export const ShieldBar = styled.div`
    border: 2px solid black;
    background-image: linear-gradient(to bottom, rgba(242,246,248,1) 0%,rgba(216,225,231,1) 50%,rgba(181,198,208,1) 51%,rgba(224,239,249,1) 100%);
    background-size: ${(props)=>{return (`${Math.floor((props.cvalue/props.maxvalue)*100)}% 100%;`)}}
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    //transition: background-size ease-out 1s 0s;
    font-size: 20px;
    color: black;
    font-family: sans-serif;
    margin-top: 5px;
    position: relative;
    &::before{
        content: "";
        position: absolute;
        top: 0; left: 0;
        width: 100%; height: 100%;
        z-index: -1;
        background-image: linear-gradient(to bottom, rgba(254,252,234,1) 0%,rgba(241,218,54,1) 100%);
        background-size: ${(props)=>{return (`${Math.floor((props.cvalue/props.maxvalue)*100)}% 100%;`)}}
        background-repeat: no-repeat;
        transition: background-size ease-out 0.75s 0.75s;
    }
`