import React ,{useState} from "react";
import {Weapon} from "./Weapon";
import {Character} from "./Character";
import {createGlobalStyle} from "styled-components";
import {XpBar} from "./styledComponents/XpBar";
import {StyledButton} from "./styledComponents/StyledButton";
import {LevelCalculator} from "./LevelCalculator";
const GlobalStyles = createGlobalStyle`
        body{
        overflow-y: auto!important;
        }
        div#profile{
            width: 250px;
            height: 120px;
            position: absolute;
            left: 0;
            top : 0;
            background-color: rgba(255,255,255,0.7);
            border-radius: 20px;
            box-shadow: 0 0 10px 10px rgba(255,255,255,0.7);
            font-size: 26px;
            font-family: Tisa sans-serif;
            text-align: left;
            padding: 10px 30px;
        }
        span#level{
            background-image: url("https://cdn.discordapp.com/attachments/696274487657758720/999698453808558171/156599-vector-star-gold-free-transparent-image-hq.png");
            background-size: 60px 60px;
            display: inline-block;
            background-repeat: no-repeat;
            width: 60px;
            height: 60px;
            text-align: center;
            padding-top: 13px;
            font-size: 26px;
            font-family: Tisa sans-serif;
        }
        h1{
        margin: 120px 0 20px 0;
        text-align: center;
        font-family: 'Dancing Script', cursive;
        font-size: 80px;
        color: white;
        text-shadow: 0 0 10px green;
        }
        
    `
function MainMenu(props){
    let levelInfo = LevelCalculator(props.dataControl.Data.xp);
    return(
        <div>
            <GlobalStyles/>
            <div id="profile">
                <div style={{display:"flex",justifyContent:"flex-start",alignItems:"center"}}>
                    <span id="level">
                        {levelInfo.level}
                    </span>
                    <XpBar maxvalue={levelInfo.limit} cvalue={levelInfo.remaining}>{levelInfo.remaining}/{levelInfo.limit}</XpBar>
                </div>
                <div>
                    <svg style={{color:"green"}} xmlns="http://www.w3.org/2000/svg" width="32" height="32" fill="currentColor"
                         className="bi bi-coin" viewBox="0 0 16 16">
                        <path
                            d="M5.5 9.511c.076.954.83 1.697 2.182 1.785V12h.6v-.709c1.4-.098 2.218-.846 2.218-1.932 0-.987-.626-1.496-1.745-1.76l-.473-.112V5.57c.6.068.982.396 1.074.85h1.052c-.076-.919-.864-1.638-2.126-1.716V4h-.6v.719c-1.195.117-2.01.836-2.01 1.853 0 .9.606 1.472 1.613 1.707l.397.098v2.034c-.615-.093-1.022-.43-1.114-.9H5.5zm2.177-2.166c-.59-.137-.91-.416-.91-.836 0-.47.345-.822.915-.925v1.76h-.005zm.692 1.193c.717.166 1.048.435 1.048.91 0 .542-.412.914-1.135.982V8.518l.087.02z"/>
                        <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
                        <path d="M8 13.5a5.5 5.5 0 1 1 0-11 5.5 5.5 0 0 1 0 11zm0 .5A6 6 0 1 0 8 2a6 6 0 0 0 0 12z"/>
                    </svg> {props.dataControl.Data.coin}
                </div>
            </div>
            <h1>Simple Impact</h1>
            <div style={{display:"flex",flexDirection: "column",gap:"20px",alignItems:"center"}}>
                <StyledButton width={250} height={75} onClick={()=>{if(props.dataControl.Data.selectedCharacter.toString().charAt(0)!=="c" || props.dataControl.Data.selectedWeapon.toString().charAt(0)!=="w"){alert("You must select a character and a weapon before starting!")}else{props.sceneControl.setScene("AreaSelection")}}}>Start Playing</StyledButton>
                <StyledButton width={250} height={75} onClick={()=>{props.sceneControl.setScene("CharacterSelection")}}>Change Character</StyledButton>
                <StyledButton width={250} height={75} onClick={()=>{props.sceneControl.setScene("WeaponSelection")}}>Change Weapon</StyledButton>
                <StyledButton width={250} height={75} onClick={()=>{props.sceneControl.setScene("Shop")}}>Shop</StyledButton>
            </div>
        </div>
    )
}
export default MainMenu