import styled from "styled-components";
export const DamageBar = styled.div`
    border: 2px solid black;
    background-image: linear-gradient(to bottom, rgba(248,80,50,1) 0%,rgba(241,111,92,1) 50%,rgba(246,41,12,1) 51%,rgba(240,47,23,1) 71%,rgba(231,56,39,1) 100%);
    background-size: ${(props)=>{return (`${Math.floor((props.cvalue/props.maxvalue)*100)}% 100%;`)}}
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    font-size: 20px;
    color: white;
    font-family: sans-serif;
    margin-top: 5px;
    position: relative;
`