import styled from "styled-components";
export const SelectButton = styled.button`
    font-size: 1.5em;
    font-weight: 600;
    border-radius: 20px;
    border: 1px solid black;
    padding: 0 15px;
    ${(props) => {if(props.selected){return "color: antiquewhite; background-color: #000159; border-color:white;"}else{return `color: #000159;
    background-color: antiquewhite;
    &:hover{
        background-color: #cac4ff;
    }
    &:active{
        color: antiquewhite;
        background-color: #000159;
        border-color: white;
    }`}}}
    `