import {LevelCalculator} from "./LevelCalculator";

export class GreenForestEnemy{
    gameinfo;
    datainfo;
    constructor(gameinfo,datainfo) {
        this.gameinfo = gameinfo;
        this.datainfo = datainfo;
    }
    name = "";
    baseHp = 0;
    baseDmg = 0;
    rewardXp = 0;
    rewardCoin = 0;
    shield = false;
    imgsrc = "";
    enemyPassive(){

    }
    makeEnemy(){
        let chance = Math.random()*100;
        //console.log(this.datainfo.xp)
        if(chance>90){
            let chance2 = Math.random();
            if(chance2>0.5){
                return new KeeperOfForest(this.gameinfo,this.datainfo);
            }else {
                return new Enchantress(this.gameinfo, this.datainfo);
            }
        }else if(chance>65){
            let chance2 = Math.random();
            if(chance2>0.5){
                return new LeafFairy(this.gameinfo,this.datainfo);
            }else {
                return new ForestSniper(this.gameinfo, this.datainfo);
            }
        }else{
            let chance2 = Math.random();
            if(chance2>0.5){
                return new Froggy(this.gameinfo,this.datainfo);
            }else {
                return new Slime(this.gameinfo, this.datainfo);
            }
        }
    }
}
class Slime extends GreenForestEnemy{
    ginfo;
    dinfo;
    level;
    baseHp;
    baseDmg;
    rewardXp;
    rewardCoin;
    constructor(gameinfo,datainfo) {
        super(gameinfo,datainfo);
        this.ginfo = gameinfo;
        this.dinfo = datainfo;
        this.level = LevelCalculator(datainfo.xp).level;
        this.baseHp = 200 + (25*Math.floor(this.level/10));
        this.baseDmg = 20 + (2*Math.floor(this.level/10));
        this.rewardXp = 5;
        this.rewardCoin = Math.floor(this.baseHp/5);
    }
    name = "Slime";
    imgsrc = "https://cdn.discordapp.com/attachments/696274487657758720/1000837088050155560/slime.png";
}
