import styled from "styled-components";
export const CharacterCard = styled.div`
    height: 400px;
    min-height: 250px;
    max-width: 350px;
    min-width: 300px;
    border: 8px groove ${(props) => {if(props.star==4){return "#00a7ff"}else if(props.star==5){return "#bd1fff"}else if(props.star==3){return "#7e7e7e"}}};
    background-image: url("${props => {return props.imgsrc}}");
    background-size: auto 100%;
    background-repeat: no-repeat;
    background-color: rgba(255,255,255,0.3);
    backdrop-filter: blur(12px);
    background-origin: border-box;
    background-position: center center;
    box-sizing: border-box;
    margin-top: 20px;
    border-radius: 10px;
    transition: background-size 0.5s ease 0s,background-position 0.5s ease 0s;
    &:hover{
        background-size: auto 125%;
        background-position: top center;
    }
`
