import styled from "styled-components";
export const XpBar = styled.div`
    border: 1px solid black;
    background-image: linear-gradient(to bottom, rgba(208,228,247,1) 0%,rgba(115,177,231,1) 24%,rgba(10,119,213,1) 50%,rgba(83,159,225,1) 79%,rgba(135,188,234,1) 100%);
    background-size: ${(props)=>{return (`${Math.floor((props.cvalue/props.maxvalue)*100)}% 100%;`)}}
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    transition: background-size ease-out 1s 0s;
    font-size: 20px;
    border-radius: 50px;
    width:130px;
`