                this.ginfo.setEnemySideText(<><FloatingMessage><span className="gray-text">-{hitDmg} Shield<br/>({Math.floor((1.2+0.05*(this.level-1))**this.stackOfBravery*100-100)}% Bonus)</span></FloatingMessage></>)
