import styled from "styled-components";
export const HpBar = styled.div`
    border: 2px solid black;
    background-image: ${(props)=>{if(props.type==="hero"){return "linear-gradient(to bottom, rgba(157,213,58,1) 0%,rgba(161,213,79,1) 50%,rgba(128,194,23,1) 51%,rgba(124,188,10,1) 100%)"}else if(props.type==="enemy"){return "linear-gradient(to bottom, rgba(248,80,50,1) 0%,rgba(241,111,92,1) 50%,rgba(246,41,12,1) 51%,rgba(240,47,23,1) 71%,rgba(231,56,39,1) 100%)"}}};
    background-size: ${(props)=>{if(props.cvalue<0){return "0% 100%;"}else{return (`${Math.floor((props.cvalue/props.maxvalue)*100)}% 100%;`)}}}
    height: 25px;
    display: flex;
    justify-content: center;
    align-items: center;
    background-repeat: no-repeat;
    //transition: background-size ease-out 1s 0s;
    font-size: 20px;
    color: white;
    font-family: sans-serif;
    margin-top: 5px;
    position: relative;
    &::before{
        content: "";
        position: absolute;
        top: 0; left: 0;
        width: 100%; height: 100%;
        z-index: -1;
        background-image: linear-gradient(to bottom, rgba(254,252,234,1) 0%,rgba(241,218,54,1) 100%);
        background-size:  ${(props)=>{if(props.cvalue<0){return "0% 100%;"}else{return (`${Math.floor((props.cvalue/props.maxvalue)*100)}% 100%;`)}}}
        background-repeat: no-repeat;
        transition: background-size ease-out 0.75s 0.75s;
    }
`