import styled,{keyframes} from "styled-components";
const revealing = keyframes`
    0% {filter:brightness(0%)}
    75% {filter:brightness(5%)}
    100% {filter:brightness(100%)}
`
const scaling = keyframes`
    0% {transform:scale(50%);box-shadow: 0px 0px 10px 0px antiquewhite;}
    75% {transform:scale(60%);box-shadow: 0px 0px 10px 5px antiquewhite;}
    100% {transform:scale(100%);box-shadow: 0px 0px 15px 15px antiquewhite;}
`
const shadowing = keyframes`
    0% {transform:scale(100%);box-shadow: 0px 0px 15px 15px antiquewhite;}
    100% {transform:scale(100%);box-shadow: 0px 0px 15px 5px antiquewhite;}
`
export const RevealingCard = styled.div`
    &::before{
    content: "";
  position: absolute;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background-image: url("${props => {return props.imgsrc}}");
  background-size: contain;
  background-repeat: no-repeat;
  background-origin: border-box;
  background-position: center;
    box-sizing: border-box;
  animation-name : ${revealing};
  animation-duration : 4s;
  filter: brightness(100%);
    }
    background-color: black;
    position: relative;
    height: 500px;
    width: 400px;
    border: 12px ridge ${(props) => {if(props.star==4){return "#00a7ff"}else if(props.star==5){return "#bd1fff"}else if(props.star==3){return "#7e7e7e"}}};
    box-sizing: border-box;
    animation-name: ${scaling};
    animation-duration: 4s;
    &::after{
        content: "";
  position: absolute;
  top: -12px; left: -12px;
  width: 400px;
  height: 500px;
  z-index: -1;
            animation-name: ${shadowing};
            animation-duration: 1.5s;
            animation-direction: alternate;
            animation-iteration-count: infinite;
            animation-delay: 4s;
    }
`
