import {LevelCalculator} from "./LevelCalculator";
import {FloatingMessage} from "./styledComponents/FloatingMessage";
import React from "react";

export class DarkOceanEnemy{
    gameinfo;
    datainfo;
    name = "";
    baseHp = 0;
    baseDmg = 0;
    rewardXp = 0;
    rewardCoin = 0;
    shield = false;
    imgsrc = "";
    constructor(gameinfo,datainfo) {
        this.gameinfo = gameinfo;
        this.datainfo = datainfo;
    }
    enemyPassive(){

    }
    enemyAttack(){

    }
    updateginfo(newinfo){

    }
    makeEnemy(){
        let chance = Math.random()*100;
        //console.log(this.datainfo.xp)
        if(chance>90){
            let chance2 = Math.random();
            if(chance2>0.5){
                return new Harvia(this.gameinfo,this.datainfo);
            }else {
                return new Harvia(this.gameinfo, this.datainfo);
            }
        }else if(chance>70){
            let chance2 = Math.random();
            if(chance2>0.5){
                return new MoonClaw(this.gameinfo,this.datainfo);
            }else {
                return new MoonClaw(this.gameinfo, this.datainfo);
            }
        }else if(chance>40){
            let chance2 = Math.random();
            if(chance2>0.5){
                return new SerpentWitch(this.gameinfo,this.datainfo);
            }else {
                return new SerpentWitch(this.gameinfo, this.datainfo);
            }
        }else{
            let chance2 = Math.random();
            if(chance2>0.5){
                return new DeathFairy(this.gameinfo,this.datainfo);
            }else {
                return new DeathFairy(this.gameinfo, this.datainfo);
            }
        }
    }
}
class DeathFairy extends DarkOceanEnemy{
    ginfo;
    dinfo;
    level;
    baseHp;
    baseDmg;
    rewardXp;
    rewardCoin;
    constructor(gameinfo,datainfo) {
        super(gameinfo,datainfo);
        this.ginfo = gameinfo;
        this.dinfo = datainfo;
        this.level = LevelCalculator(datainfo.xp).level;
        this.baseHp = 600 + (60*Math.floor(this.level/10));
        this.baseDmg = 50 + (5*Math.floor(this.level/10));
        this.rewardXp = 10
        this.rewardCoin = Math.floor(this.baseHp/1.75);
    }
    name="Death Fairy";
    imgsrc = "https://media.discordapp.net/attachments/1016773280658632745/1016773328712765470/death_fairy.jpg?width=518&height=604";
    healpassive = false;
    enemyAttack() {
        //removing extra buffs
        const enemyBuff = document.querySelector("#enemyBuff");
        for(let enemybuff of enemyBuff.children){
            enemyBuff.removeChild(enemybuff);
        }
        let hitDmg = this.ginfo.enemyDamage - Math.floor((Math.random()-0.5)*(this.ginfo.enemyDamage/10));
        this.ginfo.setHeroSideText(<> </>);
        if(this.ginfo.SelectedCharacter.shield){
            this.ginfo.setHeroSideText(<><FloatingMessage><span className="gray-text">-{hitDmg} Shield</span></FloatingMessage></>)
            this.ginfo.setHeroShieldHp(prevState => {
                return prevState - hitDmg
            });
        }else {
            this.ginfo.setHeroSideText(<><FloatingMessage><span className="red-text">-{hitDmg} Health</span></FloatingMessage></>)
            this.ginfo.setHeroHp(prevState => {
                return prevState - hitDmg
            });
        }
    }
    updateginfo(newginfo){
        this.ginfo = newginfo;
        if(this.ginfo.enemyHp < this.ginfo.enemyBaseHp/5 && this.ginfo.enemyHp>0 && !this.healpassive) {
            //console.log("activated");
            this.ginfo.setEnemyShield(true);
            this.ginfo.setEnemyBaseShield(this.ginfo.enemyBaseHp / 10);
            this.ginfo.setEnemyShieldHp(this.ginfo.enemyBaseHp / 10);
            this.healpassive = true;
        }
    }
}
class SerpentWitch extends DarkOceanEnemy{
    ginfo;
    dinfo;
    level;
    baseHp;
    baseDmg;
    rewardXp;
    rewardCoin;
    shield = true;
    baseShield = 0;
    constructor(gameinfo,datainfo) {
        super(gameinfo,datainfo);
        this.ginfo = gameinfo;
        this.dinfo = datainfo;
        this.level = LevelCalculator(datainfo.xp).level;
        this.baseHp = 700 + (60*Math.floor(this.level/10));
        this.baseDmg = 55 + (7*Math.floor(this.level/10));
        this.rewardXp = 20
        this.rewardCoin = Math.floor(this.baseHp/0.8);
        this.baseShield = 300 + (30*Math.floor(this.level/10));
    }
    name="Serpent Witch";
    imgsrc = "https://media.discordapp.net/attachments/1016773280658632745/1016997550986895441/Serpent_Witch.jpg?width=453&height=604";
    enemyAttack() {
        //removing extra buffs
        const enemyBuff = document.querySelector("#enemyBuff");
        for(let enemybuff of enemyBuff.children){
            if(enemybuff.id!=="SerpantwitchBuff"){
                enemyBuff.removeChild(enemybuff);
            }
        }
        let hitDmg = this.ginfo.enemyDamage - Math.floor((Math.random()-0.5)*(this.ginfo.enemyDamage/10));
        if(this.shield){
            hitDmg = Math.floor(hitDmg*1.5);
            //applying buff
            const enemyBuff = document.querySelector("#enemyBuff");
            if(enemyBuff.querySelector("#SerpantwitchBuff")===null){
                let newnode = document.createElement("div");
                newnode.id = "SerpantwitchBuff";
                newnode.innerHTML = `<div class="buffBox" style='background-image: url("https://media.discordapp.net/attachments/1016773280658632745/1017013016631459840/serpent_witch_passive.jpg")'>
                            
                    </div>
                    <div class="buffTooltip">
Dealing extra damage.
                    </div>`
                newnode.firstChild.addEventListener("mouseenter",()=> {
                        document.querySelector("#SerpantwitchBuff .buffTooltip").style.opacity = "100%";
                    }
                )
                newnode.firstChild.addEventListener("mouseout",()=>{
                    document.querySelector("#SerpantwitchBuff .buffTooltip").style.opacity = "0%";
                })
                enemyBuff.appendChild(newnode);
            }
        }else{
            const enemyBuff = document.querySelector("#enemyBuff");
            for(let enemybuff of enemyBuff.children){
                if(enemybuff.id==="SerpantwitchBuff"){
                    enemyBuff.removeChild(enemybuff);
                }
            }
        }
        this.ginfo.setHeroSideText(<> </>);
        if(this.ginfo.SelectedCharacter.shield){
            this.ginfo.setHeroSideText(<><FloatingMessage><span className="gray-text">-{hitDmg} Shield</span></FloatingMessage></>)
            this.ginfo.setHeroShieldHp(prevState => {
                return prevState - hitDmg
            });
        }else {
            this.ginfo.setHeroSideText(<><FloatingMessage><span className="red-text">-{hitDmg} Health</span></FloatingMessage></>)
            this.ginfo.setHeroHp(prevState => {
                return prevState - hitDmg
            });
        }
    }
    updateginfo(newginfo){
        this.ginfo = newginfo;
    }
}
class MoonClaw extends DarkOceanEnemy{
    ginfo;
    dinfo;
    level;
    baseHp;
    baseDmg;
    rewardXp;
    rewardCoin;
    constructor(gameinfo,datainfo) {
        super(gameinfo,datainfo);
        this.ginfo = gameinfo;
        this.dinfo = datainfo;
        this.level = LevelCalculator(datainfo.xp).level;
        this.baseHp = 1200 + (90*Math.floor(this.level/10));
        this.baseDmg = 80 + (10*Math.floor(this.level/10));
        this.rewardXp = 30
        this.rewardCoin = Math.floor(this.baseHp/0.5);
    }
    name="Moon Claw";
    imgsrc = "https://media.discordapp.net/attachments/1016773280658632745/1017013883791212604/Moon_Claw.jpg?width=427&height=604";
    damagebonus = 0;
    enemyAttack() {
        let hitDmg = this.ginfo.enemyDamage - Math.floor((Math.random()-0.5)*(this.ginfo.enemyDamage/10));
        if(hitDmg!==0){
            hitDmg += this.damagebonus;
        }
        this.damagebonus = this.damagebonus + 2;
        //applying buff
        const enemyBuff = document.querySelector("#enemyBuff");
        //removing extra buffs
        for(let enemybuff of enemyBuff.children){
            if(enemybuff.id!=="MoonclawBuff"){
                enemyBuff.removeChild(enemybuff);
            }
        }
        if(enemyBuff.querySelector("#MoonclawBuff")===null){
            let newnode = document.createElement("div");
            newnode.id = "MoonclawBuff";
            newnode.innerHTML = `<div class="buffBox" style='background-image: url("https://media.discordapp.net/attachments/1016773280658632745/1017014132999987210/moon_claw_passive.jpg")'>
                            
                    </div>
                    <div class="buffTooltip">
Increasing damage after every turn.
                    </div>`
            newnode.firstChild.addEventListener("mouseenter",()=> {
                    document.querySelector("#MoonclawBuff .buffTooltip").style.opacity = "100%";
                }
            )
            newnode.firstChild.addEventListener("mouseout",()=>{
                document.querySelector("#MoonclawBuff .buffTooltip").style.opacity = "0%";
            })
            enemyBuff.appendChild(newnode);
        }
        this.ginfo.setHeroSideText(<> </>);
        if(this.ginfo.SelectedCharacter.shield){
            this.ginfo.setHeroSideText(<><FloatingMessage><span className="gray-text">-{hitDmg} Shield</span></FloatingMessage></>)
            this.ginfo.setHeroShieldHp(prevState => {
                return prevState - hitDmg
            });
        }else {
            this.ginfo.setHeroSideText(<><FloatingMessage><span className="red-text">-{hitDmg} Health</span></FloatingMessage></>)
            this.ginfo.setHeroHp(prevState => {
                return prevState - hitDmg
            });
        }
    }
    updateginfo(newginfo){
        this.ginfo = newginfo;
    }
}
class Harvia extends DarkOceanEnemy{
    ginfo;
    dinfo;
    level;
    baseHp;
    baseDmg;
    rewardXp;
    rewardCoin;
    constructor(gameinfo,datainfo) {
        super(gameinfo,datainfo);
        this.ginfo = gameinfo;
        this.dinfo = datainfo;
        this.level = LevelCalculator(datainfo.xp).level;
        this.baseHp = 1500 + (100*Math.floor(this.level/10));
        this.baseDmg = 100 + (10*Math.floor(this.level/10));
        this.rewardXp = 40
        this.rewardCoin = Math.floor(this.baseHp/0.3);
    }
    shield = false;
    shieldBase = 0;
    weakened = false;
    weakenedCd = 0;
    weakenedDuration = 0;
    name="Harvia";
    imgsrc = "https://media.discordapp.net/attachments/1016773280658632745/1017096921392894002/Harvia.jpg?width=470&height=564";
    enemyAttack() {
        if(this.weakened===true && this.shield===false){
            this.weakened = false;
        }else if(this.weakened===true && this.weakenedDuration<=0){
            this.weakened = false;
            this.ginfo.setEnemyHp(prevState => prevState + Math.floor(this.baseHp/3));
            this.shield = false;
            this.ginfo.setEnemyShield(false);
        }
        let hitDmg = this.ginfo.enemyDamage - Math.floor((Math.random()-0.5)*(this.ginfo.enemyDamage/10));
        if(this.weakened){
            hitDmg = Math.floor(hitDmg/3);
            this.weakenedDuration = this.weakenedDuration - 1;
        }else{
            this.weakenedCd = this.weakenedCd + 1;
        }
        //removing extra buffs
        const enemyBuff = document.querySelector("#enemyBuff");
        for(let enemybuff of enemyBuff.children){
                enemyBuff.removeChild(enemybuff);
        }
        this.ginfo.setHeroSideText(<> </>);
        if(this.ginfo.SelectedCharacter.shield){
            this.ginfo.setHeroSideText(<><FloatingMessage><span className="gray-text">-{hitDmg} Shield</span></FloatingMessage></>)
            this.ginfo.setHeroShieldHp(prevState => {
                return prevState - hitDmg
            });
        }else {
            this.ginfo.setHeroSideText(<><FloatingMessage><span className="red-text">-{hitDmg} Health</span></FloatingMessage></>)
            this.ginfo.setHeroHp(prevState => {
                return prevState - hitDmg
            });
        }
    }
    updateginfo(newginfo){
        this.ginfo = newginfo;
        if(this.weakened===false && this.weakenedCd>=5 && this.ginfo.enemyHp<this.ginfo.enemyBaseHp/2){
            this.weakened = true;
            this.weakenedCd = 0;
            this.weakenedDuration = 3;
            this.shield = true;
            this.shieldBase = Math.floor(this.baseHp/3);
            this.ginfo.setEnemyShield(true);
            this.ginfo.setEnemyShieldHp(Math.floor(this.baseHp/3));
            this.ginfo.setEnemyBaseShield(Math.floor(this.baseHp/3));
        }


    }
}