import styled,{keyframes} from "styled-components";
const glowing = keyframes`
    0% {box-shadow: 0px 0px 10px 0px antiquewhite;}
    50% {box-shadow: 0px 0px 10px 10px antiquewhite;}
    100% {box-shadow: 0px 0px 10px 0px antiquewhite;}
`
export const PlayerCard = styled.div`
    height: 500px;
    max-width: 400px;
    border: 8px ridge ${(props) => {if(props.turn){return "#f0f71b"}else{return "#737373"}}};
    background-image: url("${props => {return props.imgsrc}}");
    background-size: contain;
    background-repeat: no-repeat;
    background-color: rgba(255,255,255,0.3);
    backdrop-filter: blur(12px);
    background-origin: border-box;
    background-position: center;
    box-sizing: border-box;
    animation-name : ${glowing};
    animation-duration : 2.5s;
    animation-iteration-count: ${(props) => {if(props.turn){return "infinite"}else{return "0"}}};
    margin-top: 20px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    font-family: 'Edu SA Beginner', cursive;
`
