import styled from "styled-components";
export const StyledButton = styled.button`
    color: black;
    background-image: linear-gradient(to bottom, rgba(210,223,237,1) 0%,rgba(200,215,235,1) 26%,rgba(190,208,234,1) 51%,rgba(166,192,227,1) 51%,rgba(175,199,232,1) 62%,rgba(186,208,239,1) 75%,rgba(153,181,219,1) 88%,rgba(121,155,200,1) 100%);
    width: ${(props => {return props.width})}px;
    height: ${(props => {return props.height})}px;
    border: 8px outset rgba(190,208,234,1);
    border-radius: 10px;
    font-size: 32px;
    font-family: 'Edu SA Beginner', cursive;
    &:hover{
    border: 8px outset rgba(121,155,200,1);
    }
    &:active{
        border: 8px inset rgba(190,208,234,1);
    }
`